 * @Description: 这是默认设置,请设置`customMade`, 打开koroFileHeader查看配置 进行设置: https://github.com/OBKoro1/koro1FileHeader/wiki/%E9%85%8D%E7%BD%AE
-->
<template>
  <div :style="{ height: screenHeight + 'px' }">
    <mavon-editor class="markdown-container"
        v-model="content"
        @fullScreen="fullScreen"
        @save="save"
        :toolbars="toolbars"
        :subfield="subfield"
        :defaultOpen="defaultOpen"
      >
      <template slot="left-toolbar-after">
        <button
          type="button"
          @click="closeView"          
          aria-hidden="true"
          title="预览"
        >预览</button>
      </template>
    </mavon-editor>
  </div>
</template>
<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {

  components:{
    mavonEditor
  },
  data: function() {
    return {
      content: "",
      html: "",
      subfield:false,
      defaultOpen:"preview",
      clientData:{},
      screenHeight: 0, // 屏幕高度
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        quote: true, // 引用
        ol: true, // 有序列表
        ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        undo: true, // 上一步
        redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: false, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
    }
    };
  },
  // components: {
  //   mavonEditor
  // },
  methods: {
    // 将图片上传到服务器，返回地址替换到md中
    fullScreen(result){
      console.log("fullScree",result)
      return true;
    },
    change(value, render) {
      // render 为 markdown 解析后的结果
      this.html = render;
    },
    save(value,render) {
      // console.log(value);
      console.log(render);
      try {
        this.clientData.content = value
        window.chrome.webview.hostObjects.csobj.SaveMemo(JSON.stringify(this.clientData))
      } catch (error) {
        alert("error")  
      }
      // alert(JSON.stringify(this.clientData))  
      
    },
    closeView(){
        if(this.subfield==false){
          this.subfield=true
        } else {
          this.subfield=false
        }
        if(this.defaultOpen=="preview"){
          this.defaultOpen="edit"
        } else {
          this.defaultOpen="preview"
        }
        console.log(this.subfield,this.defaultOpen)
      },
    loadMarkdown(){
      window.chrome.webview.addEventListener('message', arg => {
        this.clientData = arg.data;
        this.content = this.clientData.content
      });
    }
  },
  mounted(){
    this.screenWeight = document.documentElement.clientWidth;
    this.screenHeight = document.documentElement.clientHeight *0.9;
    this.loadMarkdown()
    // window.chrome.webview.hostObjects.csobj.GetMessageInfo()
    //   .then(
    //     msg => {
    //       this.content = msg;
    //     }
    // )
  }
};
</script>
<style>
.markdown-container{
  top: 0;
  left: 0;
  position: absolute;
  height: 100%;
  width: 100%;
}
</style>
